
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "myBankCard",
components: {
ViewBox,
XHeader
},
created () {
this.queryUserBankCard()
},
data() {
return {
bankCardList: []
};
},
methods: {
queryUserBankCard() {
this.$vux.loading.show();
this.$api
.queryUserBankCard({})
.then(data => {
if (data.code === 200) {
this.bankCardList = data.data.bankCards
}
this.$vux.loading.hide();
});
},
toEditBankCard(item) {
this.$router.push({
name: 'editBankCard',
params: {bankCardInfo: item}
})
}
}
};



